import React, { ChangeEvent, forwardRef, memo } from 'react';
import cn from 'classnames';

import { SwitcherProps } from './Switcher.types';

export const Switcher = memo(
  forwardRef<HTMLInputElement, SwitcherProps>(
    ({ checked, initialState = false, disabled, onSwitch = () => null, className, children }: SwitcherProps, ref) => {
      const switchCnString = cn(['thuiSwitcher', className]);
      const onChange = (event: ChangeEvent<HTMLInputElement>) => {
        onSwitch(event.target.checked);
      };

      return (
        <div className={switchCnString} ref={ref}>
          <label className="thuiSwitcher__wrapper">
            <input
              className="thuiSwitcher__input"
              checked={checked}
              onChange={onChange}
              defaultChecked={initialState}
              type="checkbox"
              disabled={disabled}
            />
            <span className="thuiSwitcher__slider" />
          </label>
          {children && (
            <span className={cn('thuiSwitcher__text', { 'thuiSwitcher__text--disabled': disabled })}>{children}</span>
          )}
        </div>
      );
    }
  )
);
